import React, { useState } from 'react';
import { Switch, BrowserRouter as Router, Route } from 'react-router-dom';

import Header from './components/Header';
import About from './components/About';
import FormTodo from './components/FormTodo';
import Todo from './components/Todo';
import './App.css';

function App() {
  // Use State
  const [todos, setTodos] = useState([
    { text: 'Take Your Breakfast', completed: false },
    { text: 'Take The Trash', complete: false },
    { text: 'Meet Your Friend For Launch', completed: false }
  ])
  // Handle Methods
  const handleDelete = index => {
    const newTodos = [...todos];
    newTodos.splice(index, 1);
    setTodos(newTodos);
  }
  // Handle Completed
  const handleCompleted = index => {
    const newTodos = [...todos];
    newTodos[index].completed = !newTodos[index].completed;
    setTodos(newTodos);
  }
  // Add Todo
  const addTodo = text => {
    const newTodos = [...todos, { text }];
    setTodos(newTodos);
  }
  // Handle Trash Color
  const [color, setColor] = useState('#000');
  const redColor = c => {
    const newColor = [...color, { c }];
    setColor(newColor);
  }
  // Return
  return (
    <Router>
      <div className="App">
        <div className="container">
          <Header />
          <Switch>
            <Route exact path="/" render={props => (
              <>
                <div class="todo-list">
                  <FormTodo addTodo={addTodo} />
                  {todos.map((todo, index) => (
                    <Todo
                      index={index}
                      key={index}
                      todo={todo}
                      redColor={() => redColor('red')}
                      handleCompleted={() => handleCompleted(index)}
                      handleDelete={() => handleDelete(index)}
                    />
                  ))}
                </div>
              </>
            )} />
            <Route path="/about" component={About} />
          </Switch>
        </div>
      </div>
    </Router>
  );
}

export default App;
